.main-nav {
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-transform: uppercase;
}
.main-nav__list {
  position: absolute;
  width: 100%;
  background-color: #ffffff;
  top: 66px;
  left: 0;
  display: none;
}
.main-nav__toggle {
  border: none;
  padding: 0;
  background-color: transparent;
  width: 24px;
  height: 18px;
  position: absolute;
  top: 23px;
  right: 20px;
}
.main-nav__toggle-line {
  position: relative;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #111111;
}
.main-nav__toggle-line::before,
.main-nav__toggle-line::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #111111;
  transform-origin: 50% 50%;
  transition: top 0.3s 0.3s, transform 0.3s;
}
.main-nav__toggle-line::after {
  top: -8px;
}
.main-nav__toggle-line::before {
  top: 8px;
}
.main-nav--opened .main-nav__toggle-line {
  background-color: transparent;
}
.main-nav--opened .main-nav__toggle-line::after {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle-line::before {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle-line::before,
.main-nav--opened .main-nav__toggle-line::after {
  top: 0;
  transition: top 0.3s, transform 0.3s 0.3s;
}
.main-nav--opened .main-nav__list {
  display: block;
}
.main-nav--nojs .main-nav__list {
  position: static;
  display: block;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
@media (min-width: 768px) {
  .main-nav {
    font-size: 18px;
    line-height: 25px;
    font-weight: 400;
  }
  .main-nav__list {
    position: static;
    display: block;
  }
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1300px) {
  .main-nav {
    font-size: 20px;
    line-height: 25px;
  }
  .main-nav__list {
    background-color: transparent;
  }
}
